@import url(https://fonts.googleapis.com/css2?family=Bilbo+Swash+Caps&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100%;
  height: 100%;
  background: url("https://images.unsplash.com/photo-1488342994276-7c3bc0742042?ixlib=rb-1.2.1&w=1000&q=80") no-repeat center center fixed;
background-size: cover;
background-image:url("https://images.unsplash.com/photo-1488342994276-7c3bc0742042?ixlib=rb-1.2.1&w=1000&q=80");

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input{
    border-radius: 10px;
}
img{
   border-radius: 50px;
   transition: 0.5s; 
}
img:hover{
    transform: scale(1.02);
}
h2{
    color: #432809;
    font-size: 60px;
    font-family: 'Bilbo Swash Caps', cursive;
}
h3{
    color: #75520a;
    font-size: 30px;
    font-family: 'Lato', sans-serif;
}
p{
    width:500px;
    color:#8d620c;
    font-size: 20px;
    font-family: 'Merriweather', serif;
}
h1{
    font-family: 'Lobster', cursive;
    color:#f6bb09;
}
